.recommend {
  padding: 70px 0 0 0; }
.recommend_flex_block {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  padding: 25px 0 25px 0; }

.recommend_block {
  max-width: 252px;
  min-width: 170px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
  margin: 10px 10px;
  padding: 5px;
  border: 1px solid rgba(34,36,38,.15);
  border-radius: 5px; }
.recommend_title {
  font-size: 19px;
  font-weight: 600;
  padding: 15px;
  color: #777777; }
.recommend_image {
  width: 100%;
  max-width: 240px; }
.recommend_description {
  font-size: 17px;
  font-weight: 600;
  padding: 5px;
  color: #777777; }
.recommend_price {
  font-size: 17px;
  font-weight: 600;
  padding: 5px;
  color: #9A010A; }

@include tablet {
  .recommend_flex_block {
    justify-content: center; } }
@include phone-tablet {
  .recommend_flex_block {
    justify-content: center; } }
@include phone {
  .recommend_flex_block {
    justify-content: center; } }
