.flex_header_menu {
  justify-content: space-around;
  align-items: flex-start;
  flex-wrap: wrap;
  width: 100%; }

.flex_header_search {
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap; }

@include phone {
  .flex_header_menu {
    flex-direction: column;
    align-items: center; }
  .flex_header_search {
    width: 100%; } }
@include phone-tablet {
  .flex_header_menu {
    flex-direction: column;
    align-items: center; }
  .flex_header_search {
    width: 100%; } }
@include tablet {
  .flex_header_menu {}

  .flex_header_search {
    width: 100%; } }
@include old-scren {
  .flex_header_menu {
    margin: 0 0 30px 0; }
  .flex_header_search {
    width: 100%; } }
