.categories_flex_block {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  flex-wrap: nowrap; }
.categories_nav {
  width: 240px;
  flex-grow: 0; }
.categories_nav_list {
  ul {
    li {
      line-height: 35px;
      a {
        color: #0F0F0F;
        padding: 5px 15px;
        width: 100%;
        display: inline-block;
        &:hover {
          background: #F2F2F2;
          padding: 5px 15px; }
        &:focus {
          background: #F2F2F2;
          padding: 5px 15px; }
        &:active {
          background: #F2F2F2;
          padding: 5px 15px; } } } } }
.categories_product {
  background: #fff;
  flex-grow: 3;
  margin: 0 0 0 10px;

  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
  flex-wrap: wrap; }

.categories_product_flex_box {
  border: 1px solid rgba(34,36,38,.15);
  border-radius: .28571429rem;
  background: #fff;
  flex-grow: 3;
  margin: 10px 0 15px 0;
  flex-basis: 100%;
  flex-grow: 5;
  width: 100%;

  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
  flex-wrap: wrap; }

.categories_filter {
  border: 1px solid rgba(34,36,38,.15);
  border-radius: .28571429rem;
  margin: 0 0 0 0;
  flex-basis: 100%;
  flex-grow: 5;
  width: 100%; }
.categories_filter_elements {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  input {
    margin: 5px 0 !important;
    padding: 0 0 0 10px;
    width: 100%;
    display: table; }
  label {
    margin: 10px 0; } }
.categories_drop {
  margin: 5px 0 !important;
  min-width: 12em !important; }


@include phone {
  .categories_product {
    margin: 0 0 0 0; }
  .categories_product_flex_box {
    justify-content: center;
    margin: 0 0; }
  .categories_flex_block {
    flex-wrap: wrap; }
  .categories_nav {
    width: 100%;
    flex-basis: 100%;
    flex-grow: 3;
    margin: 0 0 15px 0; }
  .categories_filter_elements {
    flex-direction: column;
    .categories_button {
      margin: 5px 0 10px 0 !important; } } }

@include phone-tablet {
  .categories_product {
    margin: 0 0 0 0; }
  .categories_product_flex_box {
    justify-content: center; }
  .categories_nav {
    width: 200px; }
  .categories_filter_elements {
    flex-direction: column; }
  .categories_button {
    margin: 5px 0 10px 0 !important; } }
@include tablet {
  .categories_product_flex_box {
    justify-content: center; }
  .categories_nav {
    width: 200px; }
  .categories_filter_elements {
    flex-direction: column; }
  .categories_button {
    margin: 5px 0 10px 0 !important; } }
