.product {
  background: #fff;
  flex-grow: 3;
  margin: 0 0 0 10px;
  border: 1px solid rgba(34,36,38,.15);
  border-radius: .28571429rem;

  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap; }

.round-loupe {
  border-radius: 75px;
  border: 5px solid #F0F0F0; }
.product_gallery {
  width: 55%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  padding: 10px; }
.my-foto-container {
  width: 100% !important;
  max-width: 490px !important; }
.my-foto {
  width: 20% !important;
  min-width: 55px !important;
  margin: 10px 5px;
  cursor: pointer; }
.gallery_flex_block {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-wrap: wrap; }

.product_characteristics {
  width: 40%;
  height: auto;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 10px;
  border: 1px solid rgba(34, 36, 38, 0.15);
  border-radius: .28571429rem;
  margin: 10px 10px; }
.product_title {
  font-size: 1.105em;
  font-weight: 600;
  margin: 5px 0; }
.product_list {
  padding: 0 0 0 0;
  line-height: 27px;
  li {
    font-size: 1.000em !important; }
  span {
    font-size: 1.000em !important;
    border: 1px solid rgba(34, 36, 38, 0.15);
    padding: 10px;
    display: table;
    width: 100%; } }
.product_sizes {
  display: table;
  width: 100%;
  padding: 5px;
  margin: 5px 0; }
.product_size {
  width: 100%;
  padding: 0 10px 0 10px;
  border: 1px solid rgba(34, 36, 38, 0.15); }
.product_colors {
  display: table;
  width: 100%;
  padding: 5px;
  margin: 5px 0; }
.product_color {
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: flex-start;
  flex-wrap: wrap;
  padding: 10px 0;
  border: 1px solid rgba(34, 36, 38, 0.15); }
.product_color_block {
  width: 27px;
  height: 27px;
  border-radius: 120px; }


@include phone {
  .product {
    justify-content: center;
    margin: 0; }
  .product_gallery {
    width: 100%; }
  .product_title {
    text-align: center; }
  .product_characteristics {
    width: 100%; } }
@include phone-tablet {
  .product {
    justify-content: center;
    margin: 0; }
  .product_gallery {
    width: 100%; }
  .product_title {
    text-align: center; }
  .product_characteristics {
    width: 100%; } }

@include tablet {
  .product {
    margin: 0; }
  .product_gallery {
    width: 100%; }
  .product_characteristics {
    width: 100%;
    flex-direction: row;
    flex-wrap: wrap; }
  .product_title {
    flex-basis: 100%;
    flex-grow: 5; }
  .product_list {
    flex-basis: 100%;
    padding: 0;
    margin: 0; } }
@include old-scren {
  .product {
    margin: 0; }
  .product_gallery {
    width: 100%; }
  .product_characteristics {
    width: 100%;
    flex-direction: row;
    flex-wrap: wrap; }
  .product_title {
    flex-basis: 100%;
    flex-grow: 5; }
  .product_list {
    flex-basis: 100%;
    padding: 0;
    margin: 0; } }
