.footer_bg {
  background: $color_blue;
  padding: 0 0 0 0; }
.footer {
  padding: 35px 0 25px 0;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  flex-wrap: wrap;
  line-height: 20px; }
.footer_copyright {
  font-size: 15px;
  letter-spacing: 1px;
  color: #fff; }
.red_lion {
  font-size: 12px;
  letter-spacing: 1px;
  color: #fff;
  margin: 25px 0 0 0;
  transition: 0.9s;
  &:hover {
    color: #fff !important; }
  &:hover > span {
    color: #AD1826 !important; }
  span {
    transition: 0.9s; } }
