body {
  height: 100% !important;
  background: url('img/TexturaBG.jpg') no-repeat;
  background-size: inherit; }
.page {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap; }
header {
  flex: 0 0 auto; }
section {
  flex: 1 0 auto; }
footer {
  flex: 0 0 auto; }
.wrapper {
  width: 100%;
  height: auto;
  margin: 0 auto;

  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: flex-start; }

.frame {
  width: 86.74963396778917%;
  margin: 0 auto;
  padding: 15px 15px;

  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: flex-start; }

.title_section {
  @include title_section;
  flex-basis: 100%;
  margin: 0 auto 0 auto;
  padding: 0 0 5px 0;
  border-bottom: 3px solid $color_blue; }


// media
@media screen and (min-width:1367px ) {
  .frame {
    width: 1366px; } }

@include phone;

@include phone-tablet;

@include tablet;

@include old-scren;
