.baners {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap; }

.baners_image {
  margin: 10px 5px;
  &_1 {
    flex-grow: 0; }
  &_2 {
    flex-grow: 1; }
  &_3 {
    flex-grow: 0; } }

@include phone {
  .baners_image {
    width: 100%;
    margin: 10px 0;
    &_1 {
      order: 0; }
    &_2 {
      order: 2; }
    &_3 {
      order: 1; } } }
@include phone-tablet {
  .baners_image {
    width: 47%;
    margin: 10px 5px;
    &_1 {
      order: 0; }
    &_2 {
      order: 2; }
    &_3 {
      order: 1; } } }
@include tablet {
  .baners_image {
    width: 47%;
    margin: 10px 5px;
    &_1 {
      order: 0; }
    &_2 {
      order: 2; }
    &_3 {
      order: 1; } } }
@include old-scren {
  .baners_image {
    width: 25%;
    margin: 10px 5px;
    &_1 {
      order: 0; }
    &_2 {
      order: 1; }
    &_3 {
      order: 2; } } }
