.contacts {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-direction: row;
  flex-wrap: wrap; }

.contact_map {
  width: 100%;
  max-width: 100%;
  height: auto;
  background: #ccc;
  margin: 0 auto; }

.contact_block {
  width: 49%;
  margin: 0 5px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center; }

.contact_list {
  line-height: 27px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  margin: 10px 0;
  li {
    width: 100%; }
  a {
    color: #000; } }

.contact_list_group {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin: 0 0 0 0;
  span {
    margin: 0 0 0 5px; }
  .con_map {
    margin: 0 0 0 0; }
  .con_mob {
    margin: 0 0 0 -6px; } }

@include phone {
  .contact_block {
    width: 100%;
    margin: 15px 0; }
  .contact_map {
    width: 100%; } }

@include phone-tablet {
  .contact_block {
    width: 100%;
    margin: 15px 0; }
  .contact_map {
    width: 100%; } }
@include tablet {
  .contact_block {
    width: 100%;
    margin: 15px 0; }
  .contact_map {
    width: 100%; }
  .contacts {
    justify-content: center;
    align-items: center; } }
